<template>
    <div>
          <button
        type="button"
        @click="$router.push('/driver')"
        class="btn btn-sm btn-style1 font-bold text-uppercase underline text-white"
        style="position:absolute;"
      >
        <span class="text-xl typcn typcn-arrow-left"></span> Kembali
      </button>
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#000b76" fill-opacity="1" d="M0,288L0,320L144,320L144,160L288,160L288,160L432,160L432,128L576,128L576,32L720,32L720,160L864,160L864,288L1008,288L1008,256L1152,256L1152,64L1296,64L1296,32L1440,32L1440,0L1296,0L1296,0L1152,0L1152,0L1008,0L1008,0L864,0L864,0L720,0L720,0L576,0L576,0L432,0L432,0L288,0L288,0L144,0L144,0L0,0L0,0Z"></path></svg>
    <div v-if="ttdnya.length>0" class="text-center mx-auto">
      <img :src="ttdnya" class="kinoLightBox img-fluid rounded-lg text-center mx-auto" v-viewer style="height:250px;border:1px solid grey;" alt="">
    </div>
    <p class="p-2 font-bold font-times">Tanda Tangan : </p>
    <div class="bg-white p-2  shadow-inner"  >
     
        <VueSignaturePad width="100%" height="400px" ref="signaturePad" style="border:1px solid black;" />
        <br>
        <div class="row">
            <div class="col-6 text-center">
                <button class="btn btn-sm btn-danger" @click="undo">Undo</button>
            </div>
            <div class="col-6 text-center">
                <button class="btn btn-sm btn-primary" @click="save">Save</button>
            </div>
        </div>
    </div>        
    </div>        
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
export default {
  // layout:'lembur',
  data(){
      return{
          ttd:"",
          ttdnya:""
      }
  },
  methods: {
    undo() {
      this.$refs.signaturePad.undoSignature();
    },
    b64toBlob(b64Data, contentType, sliceSize) {
            contentType = contentType || '';
            sliceSize = sliceSize || 512;

            var byteCharacters = atob(b64Data);
            var byteArrays = [];

            for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
                var slice = byteCharacters.slice(offset, offset + sliceSize);

                var byteNumbers = new Array(slice.length);
                for (var i = 0; i < slice.length; i++) {
                    byteNumbers[i] = slice.charCodeAt(i);
                }

                var byteArray = new Uint8Array(byteNumbers);

                byteArrays.push(byteArray);
            }

        var blob = new Blob(byteArrays, {type: contentType});
        return blob;
    },
    save() {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
      console.log(isEmpty);
      console.log(data);
        var ImageURL = data;
        var block = ImageURL.split(";");
        var contentType = block[0].split(":")[1];// In this case "image/gif"
        var realData = block[1].split(",")[1];// In this case "R0lGODlhPQBEAPeoAJosM...."
        var blob = this.b64toBlob(realData, contentType);
        db.collection('users').doc(this.$store.state.users.uid).collection('ttd').doc('pertama').set({ttd:data},{merge:true}).then(res=>{
          alert('Berhasil daftarkan tanda tangan digital.')
            this.$router.push('/driver')
        })
    }
  },
  mounted() {
    setTimeout(() => {
      if(this.$store.state.users){
        db.collection('users').doc(this.$store.state.users.uid).collection('ttd').doc('pertama').get().then(res=>{
          let data = res.data()
          console.log(data)
          this.ttdnya = data.ttd
          this.$forceUpdate()
        })
      }else{
        this.$router.push('/')
      }
    }, 2000);
  },
};
</script>